.cart-sec {
  display: flex;
  gap: 101px;
  margin-top: 90px;


  &__title {
    display: flex;
    gap: 14px;
    flex-direction: row;
    align-items: center;
  }

  &__cart-button-image {
    display: inline-flex;
    padding: 12px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 30px;
    background: #6d8434;
  }

  &__cart-text {
    color: #010101;
    font-family: "montreal";
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    /* 100% */
    text-transform: uppercase;
  }

  &__delete-all-button {
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: center;
    margin-top: 19px;
    margin-left: 404px;
  }

  &__delete-all-button-text {
    color: #010101;
    font-family: "montreal";
    font-size: 20px;
    font-weight: 500;
    line-height: 20px;
    /* 125% */
  }

  &__cart-list {
    margin-top: 46px;
    max-width: 520px;
    display: flex;
    flex-direction: column;
    gap: 80px;
    max-height: calc(120px * 3 + 80px * 2);
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 9px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(1, 1, 1, 0.08);
      border-radius: 20px;
    }
  }

  &__second-title {
    margin-top: 129px;
    color: #010101;
    font-family: "montreal";
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    /* 100% */
    text-transform: uppercase;
  }

  &__total-sum {
    margin-top: 40px;
    border-radius: 8px;
    background: rgba(109, 132, 52, 0.05);
    padding: 14px 28px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__total {
    color: #010101;
    font-family: "montreal";
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    /* 120% */
    margin-right: 364px;
  }

  &__sum {
    color: rgba(1, 1, 1, 0.2);
    font-family: "montreal";
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
    /* 116.667% */
  }

  &__sum-span {
    color: #010101;
    font-family: "montreal";
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    /* 100% */
    margin-left: 8px;
  }

  &__input {
    display: inline-flex;
    padding: 15px 136px 15px 20px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border-radius: 30px;
    border: 1px solid rgba(1, 1, 1, 0.3);
    color: #010101;
    font-family: "montreal";
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    /* 133.333% */
    background-color: transparent;
    margin-top: 55px;
  }

  &__checkout {
    display: inline-flex;
    padding: 15px 65px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 30px;
    background: #6d8434;
    color: #e8e8e2;
    font-family: "montreal";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    /* 120% */
    margin-left: 16px;
  }

  &__checkout-div {
    display: flex;
    flex-direction: row;
  }
}

.my-cart {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-height: 600px;
  overflow-y: auto;
  padding-right: 12px;

  &__item {
    display: flex;
    flex-direction: row;
    gap: 40px;
    position: relative;

    &__image {
      width: 120px;
      height: 120px;
      border-radius: 8px;
      object-fit: cover;
      flex-shrink: 0;
      border-radius: 10px;
      background: #fafafa;
    }

    &__info {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 12px;
      position: relative;

      &-name {
        font-family: "montreal";
        font-weight: 500;
        font-size: 18px;
        color: #010101;
      }

      &-div {
        display: flex;
        flex-direction: row;
        gap: 16px;
      }

      &-category,
      &-size {
        font-family: "montreal";
        font-weight: 400;
        font-size: 12px;
        color: rgba(1, 1, 1, 0.5);
      }

      &-category-span {
        color: #010101;
        font-family: "montreal";
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        /* 128.571% */
      }

      &-size-span {
        color: #010101;
        font-family: "montreal";
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        /* 128.571% */
      }
    }

    &__price {
      font-family: "montreal";
      font-weight: 500;
      font-size: 18px;
      color: #010101;
      flex-shrink: 0;
      margin-top: 34px;
    }

    &__remove-button {
      background: none;
      border: none;
      cursor: pointer;
      padding: 4px;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        width: 16px;
        height: 16px;
        fill: #010101;
        opacity: 0.3;
        transition: opacity 0.2s;
      }

      &:hover svg {
        opacity: 1;
      }
    }
  }
}

.my-cart__item__quantity {
  width: 64px;
  height: 24px;
  position: absolute;
  padding: 5px 16px;
  border-radius: 100px;
  outline: 1px solid rgba(1, 1, 1, 0.1);
  outline-offset: -1px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-top: 8px;
  top: 78px;
  left: 260px;
}

.qty-btn {
  width: 24px;
  height: 24px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  color: #010101;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.2s;

  &:hover {
    opacity: 0.6;
  }
}

.qty-input {
  width: 12px;
  height: 24px;
  text-align: center;
  border: none;
  font-size: 16px;
  font-family: "montreal";
  color: #010101;
  background: transparent;
  outline: none;

  &::-webkit-inner-spin-button,
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}